<template>

  <div>
    
        <h3>your data is safe with us. we will not use this information for advertisement and will be strictly used for employment purpose.</h3>
       
    <form id="msform" action="" method="post">
      
       <div class="text-danger" v-if="errors.length">
        please check below error
      <li v-for="e in errors" v-bind:key="e.id">{{ e }}</li>
       </div>
    <div v-show="step === 1">
      
        <h1>Step One</h1>
        
         <fieldset>
          <select   v-model="user.lang" v-bind:multiple="multiple" class="form-select" required aria-label="multiple select example">
            <option value="English">English</option>
            <option value="Spanish">Spanish</option>
          </select>
         <br/>
          <button class="btn btn-dark" @click.prevent="next()">Next</button>
        </fieldset>
      </div>

      <div v-show="step === 2">
       
        <h1>Step Two</h1>
      
        <fieldset>
          <h2 class="fs-title">Personal Details</h2>
          <h3 class="fs-subtitle">Tell us something more about you</h3>
       
  
          <input type="text" name="fname" v-model="user.name" required placeholder="First Name" />
          <div style="display:none;" v-if="errors">Enter your first name</div>
          <input type="text" name="lname" v-model="user.last" required placeholder="Last Name" />
          <div style="display:none;" v-if="errors">Enter your last name</div>
          <input type="text" name="phone" v-model="user.phone" placeholder="Phone"  v-mask="['(###) ###-####']"  />
          <div style="display:none;" class="danger" v-if="errors">Enter your phone name</div>
          
           <div class="alert alert-info">
  <strong>Info!</strong>phone number will be used for OTP verification.
</div> 
<datepicker v-model="picked" inputFormat="MM/dd/yyyy"/>
         


            <div class="input-group">
    <span class="input-group-addon"><i class="glyphicon glyphicon-user"></i></span>
    <input type="email" id="exampleInputEmail1" v-model="user.email" aria-describedby="emailHelp"
            placeholder="Enter email">
            <div style="display:none;" class="error" v-if="errors">Enter your  email</div>
  </div>


            <div class="fancy-form">
        <legend>Address</legend>
        <p class="instructions">Start by entering your zip code.</p>
       
        <div>
          <div class="city-wrap">
            <input type="text" name="city" id="city" disabled>
            <label for="city">City</label>
          </div>
          <div class="state-wrap">
            <input  type="text" name="state" id="state" disabled>
            <label for="state">State</label>
          </div>
          <div class="zip-wrap">
            <input v-model="user.zip" type="text" pattern="[0-9]*" maxlength="5" required name="zip" id="zip">
            <label for="zip">Zip</label>
            <p class="zip-error">Not a real zip code.</p>
          </div>
        </div>
      
     
      
      </div>
      <br/>
          <button class="btn btn-dark" @click.prevent="prev()">Previous</button>
          <button class="btn btn-dark" @click.prevent="next()">Next</button>
          
        </fieldset>



      </div>

      <div v-show="step === 3">
        <h1>Step Three</h1>
        
        <fieldset>

          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <label>Industry</label>
            </div>
            <select v-model="user.industry" class="custom-select" id="inputGroupSelect01">
              <option selected>Choose...</option>
              <option value="1">One</option>
              <option value="2">Two</option>
              <option value="3">Three</option>
            </select>
          </div>
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <label>Nearest Role you currently are in</label>
            </div>
            <select v-model="user.role" class="custom-select" id="inputGroupSelect01">
              <option selected>Choose...</option>
              <option value="1">One</option>
              <option value="2">Two</option>
              <option value="3">Three</option>
            </select>
          </div>
          <input type="text" v-model="user.company" name="fname" placeholder="Company you work for" />
          <input type="text" name="fname" v-model="user.demp" placeholder="Date you started Employment" />
          
          <input type="text" name="fname" v-model="user.salary" placeholder="Current Salary per Hour" />
          <div class="input-group-text">
            <label>Open to work currently</label>
            <input v-model="user.otw" type="checkbox" aria-label="Checkbox for following text input">
          </div><br/>
          <button class="btn btn-dark" @click.prevent="prev()">Previous</button>
          <button class="btn btn-dark" @click.prevent="next()">Next</button>
        </fieldset>





      </div>
      <div v-show="step === 4">
        <h1>Step Four</h1>
        <h2> {{ user.lang }}</h2>
        <h2> {{ user.name }} {{ user.last }}</h2>
        <h2> {{ user.email }}</h2>
        <h2> {{ user.phone }}</h2>
        <h2> {{ user.dob }}</h2>
        <h2> {{ user.zip }}</h2>
        <h2> {{ user.city }}</h2>
        <h2> {{ user.state }}</h2>
        <h2> {{ user.add }}</h2>
        <h2> {{ user.add }}</h2>
        <h2> {{ picked }}</h2>
        <h2> {{ user.industry }}</h2>
        <h2> {{ user.role }}</h2>
        <h2> {{ user.company }}</h2>
        <h2> {{ user.dus }}</h2>
        <h2> {{ user.demp }}</h2>
        <h2> {{ user.salary }}</h2>
        <h2> {{ user.otw }}</h2>
        <button class="btn btn-info" @click.prevent="prev()">Previous</button>

      </div>
    </form>
  </div>
</template>

<script>
import {mask} from 'vue-the-mask';
import $ from 'jquery'
import Datepicker from 'vue3-datepicker'
export default {
  directives:{mask},
  name: 'MyMutlistepform',
  components: {
  Datepicker
},
mounted: function() {
        
function is_int(value) {
if ((parseFloat(value) == parseInt(value)) && !isNaN(value)) {
return true;
} else {
return false;
}
}

$(".fancy-form div > div").hide();

$("#zip").keyup(function() {

// Cache
var el = $(this);

// Did they type five integers?
if ((el.val().length == 5) && (is_int(el.val()))) {

// Call Ziptastic for information
$.ajax({
url: "https://zip.getziptastic.com/v2/US/" + el.val(),
cache: false,
dataType: "json",
type: "GET",
success: function(result) {

$(".zip-error, .instructions").slideUp(200);

$("#city").val(result.city);

$("#state").val(result.state);

$(".fancy-form div > div").slideDown();

$("#zip").blur();
$("#address-line-1").show().focus();

},
error: function() {

$(".zip-error").slideDown(300);

}

});

} else if (el.val().length < 5) {

$(".zip-error").slideUp(200);

}

});
    } ,
  data() {
    return {
      picked: new Date(),
      step: 1,
      multiple: true,
      
      errors: [],
      user: {
        lang: null,
        name: null,
        last: null,
        email: null,
        add: null,
        phone: null,
        zip: null,
        city: null,
        industry: null,
        role: null,
       company: null,
        demp: null,
        salary: null,
        dus: null,
        otw: null,
      }
    }
  },
  methods: {
 
    prev() {
      this.errors='';
      this.step--;
    },
   
    next() {
      this.errors=[];
      console.warn('error',this.errors)
      if(this.step==1){
        if(!this.user.lang){
          this.errors.push('Please select an language');
          return false;
        }
      }
      
      if(this.step==2){
      
        if(!this.user.name){
          this.errors.push('Please Enter your  Name');
          //this.errors='Please Enter your Name'
          return false;
            }
        if(!this.user.last){
          this.errors.push('Please Enter your Last Name');
          //this.errors='Please Enter your Last Name'
          return false;
        }
        if(!this.user.email){
          this.errors.push('Please Enter your Email');
          //this.errors='Please Enter your Email'
          return false;
        }
        if(!this.user.phone){
         // this.errors='Please Enter your Phone'
          return false;
        }
       
      } 
    
      this.errors='';
      this.step++;
      
    }
			}
	
  
}
</script>
<style>
.glyphicon {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: "Glyphicons Halflings";
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.glyphicon-user:before {
    content: "\e008";
}  
#msform input,
#msform textarea {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 0px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
  font-family: montserrat;
  color: #2C3E50;
  font-size: 13px;
}

#msform fieldset {
  background: white;
  border: 0 none;
  border-radius: 0px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
  padding: 20px 30px;
  box-sizing: border-box;
  width: 80%;
  margin: 0 10%;

  /*stacking fieldsets above each other*/
  position: relative;
}
</style>